.container {
  width: 100%;
  background: #fff;
  height: calc(100vh - 72px - 32px);
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center; }

.formContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 304px;
  text-align: center;
  & > * {
    margin-bottom: 16px; } }

.error {
  position: absolute;
  color: red;
  bottom: -32px;
  left: 0;
  right: 0; }
