.bookingContainer, .infoContainer {
  background: #ffffff;
  border-radius: 4px;
  height: calc(100vh - 72px - 32px);
  width: 624px; }
.bookingContainer {
  padding: 0 16px; }
.infoContainer {
  width: 304px;
  margin-left: 16px;
  .imageWrapper {
    height: 240px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      height: 100%; } }
  .body {
    padding: 16px;
    & > * {
      margin-bottom: 16px; } } }

.title {
  font-size: 32px;
  line-height: 72px;
  font-weight: normal; }

// styles for dataoicker input
div[class='react-datepicker__input-container'] {
  input {
    width: 186px;
    height: 40px;
    border-radius: 2px;
    border: 1px solid #CACFDD;
    background: #fff;
    padding: 0 16px;
    font-size: 14px; } }

.input {
  width: 186px;
  margin-left: 16px; }

.map {
  background: #fff;
  [class^="table"] {
    fill: #fff;
    transition: all .3s;
    & * {
      fill: #fff !important;
      transition: all .3s; }
    &:hover {
      fill: grey !important;
      & * {
        fill: grey !important; } } }
  [class$="choosen"] {
    fill: yellow !important;
    & * {
      fill: yellow !important; } } }

.chooseBlock {
  display: flex;
  align-items: center; }

.buttonBlock {
  display: flex;
  justify-content: center;
  margin: 32px 0;
  button {
    height: 40px;
    width: 304px;
    color: #ffffff;
    background: #2478FF;
    border-radius: 4px;
    cursor: pointer; } }

.modal {
  padding: 32px; }
