.placeWrapper {
  width: 100%;
  height: 200px;
  border-bottom: 1px solid #CACFDD;
  padding: 0 32px;
  display: flex;
  .imageWrapper {
    width: 250px;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 24px;
    img {
      width: 100%; } }
  .body {
    display: flex;
    .left {
      width: 226px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 24px 0;
      margin-right: 32px; }
    .right {
      width: 364px;
      padding: 24px 0 32px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .description {
        height: 77px;
        overflow: hidden;
        text-overflow: ellipsis; } }
    .buttons {
      display: flex; } } }
.editButton, .deleteButton, .createUser {
  max-width: 170px;
  height: 40px;
  width: 304px;
  color: #ffffff;
  background: #2478FF;
  border-radius: 4px;
  cursor: pointer;
  margin: 0 0 0 auto; }
.deleteButton {
  margin-left: 32px;
  background: #ED5843; }
.empty {
  font: 32px/48px RobotoMedium;
  text-align: center;
  color: #CACFDD;
  margin: 32px 0; }

.createBlock {
  display: flex;
  justify-content: center;
  margin: 32px 0;
  .createUser {
    margin: 0; } }
