.header {
  position: sticky;
  top: 0;
  min-width: 100%;
  height: 72px;
  background: #fff;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.25);
  z-index: 2;
  .wrapper {
    max-width: 944px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between; }
  .title {
    color: #2478FF;
    font-size: 36px;
    line-height: 72px; }
  .tabs {
    display: flex;
    .tabsItem {
      line-height: 72px;
      margin-left: 32px;
      position: relative; }
    .active:after {
      content: '';
      position: absolute;
      bottom: -1px;
      width: 100%;
      height: 4px;
      background: #2478FF;
      border-top-left-radius: 2px;
      border-top-right-radius: 2px;
      left: 0; } } }
