.dropdown {
  position: relative;
  cursor: pointer;
  width: 224px;
  font-size: 14px; }

.choosenValue {
  position: relative;
  height: 40px;
  line-height: 40px;
  padding: 0 20px 0 16px;
  border: 1px solid #CACFDD;
  transition: all .3s;
  z-index: 20;
  border-radius: 2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  &:hover {
    border-color: black; }
  &:after {
    position: absolute;
    content: '';
    width: 10px;
    height: 6px;
    background: url(Assets/icons/dropdown.svg) no-repeat;
    right: 16px;
    top: calc(50% - 2px);
    transition: all .3s; }
  &.openIcon {
    border-color: #CACFDD;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    z-index: 20;
    &:after {
      transform: rotate(180deg); } }
  &:focus {
    outline: 0; } }

.choosenValueIcon {
  display: flex;
  align-items: center; }

.disabled {
  border: none;
  color: black;
  cursor: auto;
  padding-left: 0;
  &:after {
    content: none; } }

.error {
  border-color: red; }

.errorText {
  cursor: auto;
  position: absolute;
  bottom: -18px;
  left: 0;
  font: 12px/16px RobotoRegular;
  color: red;
  opacity: 0;
  transition: all .3s;
  &.show {
    opacity: 1; } }

.list {
  position: absolute;
  z-index: 19;
  width: 100%;
  transform-origin:top {}
  transform: translateY(-20px);
  visibility: hidden;
  border: 1px solid #CACFDD;
  border-top: none;
  border-radius: 2px;
  opacity: 0;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s;
  pointer-events: none;
  &.withScroll {
    max-height: 240px;
    overflow: hidden;
    overflow-y: scroll; }
  &.open {
    visibility: visible;
    opacity: 1;
    z-index: 22;
    transform: translateY(0);
    transition-delay: 0s, 0s;
    pointer-events: auto; }
  .item {
    height: 40px;
    padding: 8px 0;
    line-height: 32px;
    border-top: none;
    display: flex;
    align-items: center;
    background: #fff;
    width: 100%;
    padding-left: 16px;
    transition: all .3s;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    &:hover {
      color: blue;
      background: #CACFDD; }
    &:focus {
      outline: 0; } } }

.icon {
  width: 24px;
  height: 24px;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  margin-right: 8px; }
