.card {
  height: 264px;
  width: 216px;
  margin: 16px 0 0 16px;
  border: 1px solid #CACFDD;
  border-radius: 4px;
  .imageWrapper {
    width: 100%;
    height: 136px;
    background: green;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 100%; } }
  .body {
    height: 126px;
    padding: 12px;
    font-size: 14px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .title, .average {
      font-family: RobotoMedium; } } }
