.bookingWrapper {
  width: 100%;
  height: 80px;
  border-bottom: 1px solid #CACFDD;
  padding: 0 32px;
  display: flex;
  .column {
    display: flex;
    flex-direction: column;
    width: 200px;
    margin-right: 32px;
    justify-content: center;
    &:last-of-type {
      margin: 0; }
    & div {
      line-height: 30px; } }
  .deleteButton {
    max-width: 170px;
    height: 40px;
    width: 304px;
    color: #ffffff;
    background: #2478FF;
    border-radius: 4px;
    cursor: pointer;
    margin: 0 0 0 auto; }
  .deleteButton {
    background: #ED5843; } }

.empty {
  font: 32px/48px RobotoMedium;
  text-align: center;
  color: #CACFDD;
  margin: 32px 0; }
