.wrapper {
  padding: 16px;
  display: flex;
  flex-direction: column;
  .title {
    font: 24px/32px RobotoMedium;
    margin-bottom: 8px; }
  .label {
    font-size: 14px;
    line-height: 24px; }
  .input {
    width: 100%;
    height: 40px;
    border-radius: 2px;
    border: 1px solid #CACFDD;
    background: #fff;
    padding: 0 16px;
    font-size: 14px;
    margin-bottom: 8px; } }

.button {
  .saveButton, .canselButton {
    max-width: 170px;
    height: 40px;
    width: 304px;
    color: #ffffff;
    background: #2478FF;
    border-radius: 4px;
    cursor: pointer;
    margin: 0 0 0 auto; }
  .canselButton {
    margin-left: 32px;
    background: #ED5843; } }
