.userWrapper {
  display: flex;
  align-items: center;
  padding: 0 32px;
  height: 80px;
  border-bottom: 1px solid #CACFDD;
  .login {
    width: 250px; }
  .place {
    .placeName {
      color: #2478FF;
      cursor: pointer; } } }
.editButton, .deleteButton, .createUser {
  max-width: 170px;
  height: 40px;
  width: 304px;
  color: #ffffff;
  background: #2478FF;
  border-radius: 4px;
  cursor: pointer;
  margin: 0 0 0 auto; }
.deleteButton {
  margin-left: 32px;
  background: #ED5843; }

.createBlock {
  display: flex;
  justify-content: center;
  margin: 32px 0;
  .createUser {
    margin: 0; } }

.empty {
  font: 32px/48px RobotoMedium;
  text-align: center;
  color: #CACFDD;
  margin: 32px 0; }

