.container {
  width: 100%;
  background: #fff;
  min-height: calc(100vh - 72px - 32px);
  border-radius: 4px; }

.tabBlock {
  display: flex;
  height: 64px;
  align-items: center;
  padding: 0 32px;
  border-bottom: 1px solid #CACFDD;
  .tabItem {
    margin-right: 32px;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    &.active:after {
      content: '';
      position: absolute;
      bottom: -1px;
      width: 100%;
      height: 4px;
      background: #2478FF;
      border-top-left-radius: 2px;
      border-top-right-radius: 2px; } } }

