@import 'colors.sass';
@import 'fonts.sass';

body {
	margin: 0;
	overflow-y: scroll;
	-webkit-font-smoothing: antialiased;
	background: #f1f2f4;
	& * {
		box-sizing: border-box; } }

h1, h2, h3, h4, h5, p, a, button, div, textarea, input {
	font-family: 'RobotoRegular';
	margin: 0;
	padding: 0;
	color: #000; }

a {
	text-decoration: none;
	color: #000; }

p {
	font-size: 14px;
	margin: 0;
	padding: 0;
	font-family: inherit;
	vertical-align: baseline; }

span {
	margin: 0;
	padding: 0;
	font-family: inherit;
	vertical-align: baseline; }

button {
	font-size: 14px;
	background-color: transparent;
	border: 0 solid transparent;
	outline: none;
	cursor: pointer;
	&:active,
	&:focus {
		outline: none; } }

input {
	outline: none; }

ul {
	list-style: none; }
